.topHeader {
  background-color: var(--secondary-color);
  color: #e2e2e2;
  font-size: 16px;
  font-weight: 400;
  display: flex;
  align-items: center;
  padding: 12px 0;

  &__container {
    display: flex;
    justify-content: space-between;
  }

  &__leftSide {
    display: flex;
    gap: 20px;
    align-items: center;
  }
}
