.Footer {
  background-color: white;
}

.FooterContainer {
  display: flex;
  justify-content: space-between;
  height: 61px;
  align-items: center;
  margin: auto;
  flex-wrap: wrap;
}

.FooterAgreement {
  display: flex;
  gap: 20px;
}

.AgreementText {
  font-weight: 600;
  font-size: 16px;
  color: var(--secondary-color);
}

.RightsText {
  font-weight: 400;
  font-size: 16px;
}
